<template>
	<div class="wrapper">
		<div ref="mytop">
			<v-top></v-top>
			<v-nav></v-nav>
		</div>
		<div class="content" ref="content">
			<transition name="move" mode="out-in">
				<router-view></router-view>
			</transition>
		</div>
	</div>
</template>

<script>
	import vTop from './top.vue';
	import vNav from './nav.vue';
	export default {
		components: {
			vTop,
			vNav
		},
		mounted () {  
	    this.init();  
	  },  
  	methods:{  
	    init() {  
	      const self = this;  
	      let topH = self.$refs.mytop.offsetHeight;  //获取头部的总高度
	      console.log(topH)
	      self.$refs.content.style.top = topH+'px';  //设置内容版块的top位置
	    }  
	  }  
	}
</script>

<style>
	body {
		width: 100%;
		height: 100%;
		background: rgb(50, 65, 90);
	}
</style>


