<template>
	<div class="nav">
		<el-menu 
			:default-active="onRoutes" 
			class="el-menu-demo" 
			mode="horizontal" 
			unique-opened router
			text-color="#fff"
  		active-text-color="#fff">
		  <template v-for="nav in navBar">
				<template v-if="nav.subs">
					<el-submenu :index="nav.index">
						<template slot="title">{{ nav.title }}</template>
						<el-menu-item v-for="(subNav,i) in nav.subs" :key="i" :index="subNav.index">{{ subNav.title }}</el-menu-item>
					</el-submenu>
				</template>
				<template v-else>
					<el-menu-item v-if="nav.ok" :index="nav.index">{{ nav.title }}</el-menu-item>
				</template>
			</template>
		</el-menu>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				navBar: []
			}
		},
		created () {
	  	this.$axios.get('/api/navBar').then(res => {   //请求接口webName数据
				this.navBar = res.data.data;
				console.log(this.navBar);
			}).catch(err => {
				console.log(err);
			});
	  },
		computed: {
			onRoutes() {
				return this.$route.path.replace('/', '');
			}
		}
	}
</script>

<style scoped>
	.nav {
		display: block;
		width: 100%;
		background: rgb(255,144,0);
	}
	.nav>ul{
		margin: 0 auto;
		border: none;
		background: rgb(255,144,0);
	}
	@media (min-width: 1201px) {
		.nav>ul {
			width: 74%;
		}
	}
	@media (max-width: 1200px) {
		.nav>ul {
			width: 96%;
		}
	}
	.nav>ul>li{
		font-size: 20px;
	}
	.nav .el-dropdown-menu__item--divided:before, 
	.el-menu, 
	.nav .el-menu--horizontal>.el-menu-item:not(.is-disabled):focus, 
	.nav .el-menu--horizontal>.el-menu-item:not(.is-disabled):hover, 
	.nav .el-menu--horizontal>.el-submenu .el-submenu__title:hover{
		background: rgb(255,122,21);
		height: 60px;
		line-height: 60px;
	}
	.nav .el-menu-item:focus, .el-menu-item:hover{
		background: rgb(255,122,21);
		height: 60px;
		line-height: 60px;
	}
	.nav .el-menu--horizontal>.el-menu-item{
		border: none;
		height: 60px;
		line-height: 60px;
	}
	.nav .el-menu--horizontal>.el-menu-item.is-active{
		border: none;
		background: rgb(255,122,21);
		height: 60px;
		line-height: 60px;
	}
</style>


