<template>
  <div class="tip">
  	<div class="top">
  		<div class="top_left">
	  		<img :src="webName.webLogo" />
	  	</div>
	  	<div class="top_right">
	  		<a href="#" >APP下载</a>
		    <a href="#" >车企注册</a>
		    <a href="#" >车平台登录</a>
	  	</div>
	  	<div style="clear: both;"></div>
  	</div>
  	<div class="name">{{webName.webTitle}}</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
    	webName: []
    }
  },
  created () {
  	this.$axios.get('/api/webName').then(res => {   //请求接口webName数据
			this.webName = res.data.data;
			console.log(this.webName);
		}).catch(err => {
			console.log(err);
		});
  }
}
</script>

<style>
	body{
		background: #fff;
	}
	.tip {
		display: block;
		width: 100%;
		margin: 0 auto;
	}
	@media (min-width: 1201px) {
		.tip {
			width: 74%;
		}
	}
	@media (max-width: 1200px) {
		.tip {
			width: 96%;
		}
	}
	.top {
		height: 59px;
		border-bottom: 1px solid rgb(255,144,0);
	}
	.top_left{
		float: left;
	}
	.top_left img{
		height: 30px;
		margin-top: 15px;
	}
	.top_right{
		float: right;
		margin-top: 25px;
	}
	.top_right a{
		margin-left: 20px;
		font-size: 16px;
	}
	.name{
		height: 60px;
		line-height: 60px;
	}
	@media (min-width: 1201px) {
		.name{
			font-size: 30px;
		}
	}
	@media (max-width: 1200px) {
		.name{
			font-size: 20px;
		}
	}
	
</style>



